
.superInput {
    width:200px;
    padding:10px;
    margin:10px 0;
    border:0;
    box-shadow:0 0 15px 4px rgba(0,0,0,0.06);
    border-radius:10px;
}
.errorInput {
    box-shadow: 0 0 0 0.25rem rgba(220, 53, 69, 0.25);
    width:200px;
    padding:10px;
    margin:10px 0;
    border-radius:10px;
    border:3px;
}
.errorInput:focus {
    width:200px;
    padding:10px;
    margin:10px 0;
    box-shadow:0 0 15px 4px rgba(0,0,0,0.06);
    border-radius:10px;

    outline:none !important


}

.error {
    color: red;
}
.spanClassName{
    color: red;
    font-size: 15px;
}
